.reviews {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 10px;
  padding: 10px;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

.reviews h4 {
  margin-bottom: 10px;
}

.reviews li {
  padding: 15px;
  border-radius: 4px;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px,
    rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}
